.searchview{
  background-color: #3A9425;
}
.App-logo {
  height: 50px;
  float: left;
}
.App-logo2{
  height:60px;
  margin-bottom:20px;
}

.container{
    background-color:#ffffff;
    padding-top:100px;
    padding-bottom:50px;
    margin-bottom:50px;
}

.input-group{
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
  align-items: stretch;
  width: 90%;
  margin: auto;
}
.input-group2{
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
  align-items: stretch;
  width: calc(100% - 115px);
    float: right;
    padding-right:34px;
}
.input-group-append, .input-group-prepend{
  display: -ms-flexbox;
  display: flex;
  margin-left: -1px;
}
.App-header {
  background-color: #3A9425;
  /* height: 50px; */
  padding: 5px 15px;
  color: white;
  position: fixed;
  width: 100%;
  z-index:999;
}

.star{
    height:25px;
    width:25px;
    margin-right:10px;
}

.rating img,
.rating div{
  display:inline-block;
}

.movieimage{
  width:205px;
  height:305px;
  float:left;
}
.searchview{
  height:100vh;
  width:100vw;
  text-align: center;
}
.displayinfo{
  text-align: center;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
}
.movieinput{
  height:34px;
}
.required{
  color:red;
}

textarea {
  overflow:auto;
  resize: none;
}

.commentbox{
  border:1px solid #000000;
  padding:20px;
}
/*.searchbar{
  position:fixed;
}*/
.btn-success{
  margin-top:10px;
  margin-bottom:20px;
}

.popup{
  text-align: left;
  overflow:auto;
  width: 90%;
  margin: auto;
  position: fixed;
  margin-left: 5%;
height: 40vh;
}
.popup2{
  overflow: auto;
    width: 90%;
    position: fixed;
    margin-top: 34px;
        width: calc(100% - 188px);
}

.list-group-item{
  background-color:rgba(58, 148, 37, 0.7);
  color:white;
}



